import React from 'react';
import { createRoot } from 'react-dom/client';
import './index.css';
import App from './App';
// Отключаем StrictMode для производственной среды, чтобы избежать двойного рендеринга
// и импортируем reportWebVitals только в режиме разработки
const isDevelopment = process.env.NODE_ENV === 'development';

const root = createRoot(document.getElementById('root'));
root.render(
  isDevelopment ? (
    <React.StrictMode>
      <App />
    </React.StrictMode>
  ) : (
    <App />
  )
);

// Импортируем и используем reportWebVitals только в режиме разработки
if (isDevelopment) {
  import('./reportWebVitals').then(({ default: reportWebVitals }) => {
    reportWebVitals(console.log);
  });
}

