import React, { lazy, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { LanguageProvider } from './context/LanguageContext';
import ImagePreloader from './components/ImagePreloader';

// Ленивая загрузка компонентов для улучшения производительности
const Home = lazy(() => import('./pages/index'));
const About = lazy(() => import('./pages/about'));

// Loading indicator component
const LoadingSpinner = () => (
  <div className="flex justify-center items-center min-h-screen bg-sayqal-light">
    <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-sayqal-gold"></div>
  </div>
);

// ScrollToTop component to ensure pages start at the top when navigating
function ScrollToTop() {
  const { pathname } = useLocation();
  
  React.useEffect(() => {
    // Плавная прокрутка вверх
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  }, [pathname]);
  
  return null;
}

function App() {
  return (
    <LanguageProvider>
      <Router>
        <ImagePreloader />
        <ScrollToTop />
        <Suspense fallback={<LoadingSpinner />}>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/about" element={<About />} />
          </Routes>
        </Suspense>
      </Router>
    </LanguageProvider>
  );
}

export default App;
